import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

//initialState
const initialState = {
  todos: [],
  isLoading: false,
  isSuccess: false,
  error: null,
};

//Thunk 
export const __addBtn = createAsyncThunk(
  'addBtn',
  async (payload, thunkAPI) => {
    try {
      const { data } = await axios.post('http://localhost:3001/todos', payload);
      return thunkAPI.fulfillWithValue(data);
    } catch (err) {
      //error  .
      return thunkAPI.rejectWithValue(err);
    }
  }
);

//reducer
const addtodoSlice = createSlice({
  name: 'addTodos',
  initialState,
  reducers: {
    addBtn: (state, action) => {
      return { todo: [...state.todos, action.payload] };
    },
    completeTodo: (state, action) => {
      state.isSuccess = false;
    },
  },

  extraReducers: {
    //pending - 
    [__addBtn.pending]: (state) => {
      //promise fullfilled dispatch.
      //    true .
      state.isLoading = true;
    },
    //
    [__addBtn.fulfilled]: (state, action) => {
      //  , false .
      state.isLoading = false;
      //Store  addtodo  todo .
      state.todos = action.payload;
    },

    [__addBtn.rejected]: (state, action) => {
      // ,   ,
      //false 
      state.isLoading = false;
      //catch  error  state.error .
      state.error = action.payload;
    },
  },
});

export const { addBtn, completeTodo } = addtodoSlice.actions;
export default addtodoSlice.reducer;
